#intro{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
}

.profileImg{
padding: 5px;
}

.bg{
    position: absolute;
    z-index: -1;
    top: 4rem;
    right:0;
    object-fit: cover;
    height: 100vh;
}

.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.hello{
    font-size: 1.5rem;
    font-weight: 100;
}

.introName{
    color: yellow;

}

.introPara{
    font-size: medium;
    font-weight: 100; 
}

.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem ;
    border-radius: 2rem;
    border: none;
    cursor: pointer;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1cqmax;
    width: 1rem;
    cursor: pointer;
}

@media screen and (max-width:840px) {
    .bg{
        right: -10vw;
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}

@media screen and (max-width:480px) {
    .bg{
        right: -10vw;
    }
}   